.title {
  margin: 0;
  font-weight: 800;
  font-size: 64px;
  line-height: 70px;
  color: #ffffff;
  overflow-wrap: break-word;

  @media (max-width: $tablet-width) {
    font-size: 48px;
    line-height: 58px;
  }

  @media (max-width: $mobile-width) {
    font-size: 36px;
    line-height: 40px;
  }
}
